export default {
	mounted(){
		uni.$on('userChoiceReset',()=> {
			this.currentIndex = 0;
		})
	},
	data(){
		return{
			currentIndex:0,
			choiceData:[
				{
					id:0,
					name:'现金'
				},
				{
					id:1,
					name:'微信钱包'
				},
				{
					id:2,
					name:'支付宝'
				},
				{
					id:3,
					name:'储蓄卡'
				},
				{
					id:4,
					name:'信用卡'
				},
				
				
			]
		}
	},
	methods:{
		itemClick(e){
			this.currentIndex = Number(e.target.id);
			this.$emit('userChoice',this.choiceData[this.currentIndex].name)
		}
	}
}
